import React from "react";
// import { Link } from "react-router-dom";
import "./Nav.css";
import { Navbar, Modal, Input, NavItem, Row, Button } from 'react-materialize'
import API from '../../utils/API'

class Nav extends React.Component {
  state = {
    firstName: "",
    lastName: "",
    email: "",
    password: ""
  };

  handleInputChange = event => {
    const { name, value } = event.target;
    this.setState({
      [name]: value
    });
  };

  handleFormSubmit = () => {
    
    if (this.state.email && this.state.password) {
      API.saveUser({
        firstName: this.state.firstName,
        lastName: this.state.lastName,
        email: this.state.email,
        password: this.state.password
      })
        .then(res => this.loadBooks())
        .catch(err => console.log(err));
    }
  };

  render () {
    return (
      <div className="navbar-fixed animated fadeInDown">
          <Navbar brand='BitPop.me!' className="#03a9f4 light-blue">
                  <Modal
                    header='Sign in'
                    fixedFooter
                    trigger={<NavItem className="right" onClick={() => console.log('test click')}>Get Started</NavItem>}>
                      <Row>
                        <Input placeholder="Name@email.com" s={12} label="Email" type="email"/>
                        <Input type="Password" label="password" s={12} />
                        <Button waves='light' className="#03a9f4 light-blue">Submit</Button>
                      </Row>
                  </Modal>
                
                  <Modal
                    header='Sign up'
                    fixedFooter
                    trigger={<NavItem className="right" href='components.html'>Sign Up</NavItem>}>
                    <Row>
                      <form>
                        <Input s={6} label="First Name" value={this.state.firstName} onChange={this.handleInputChange} name="first name" />
                        <Input s={6} label="Last Name" value={this.state.lastName} onChange={this.handleInputChange} />
                        <Input placeholder="Name@email.com" s={12} label="Email" type="email" value={this.state.email} onChange={this.handleInputChange} />
                        <Input type="Password" label="password" s={12} value={this.state.password} onChange={this.handleInputChange} />
                        <Button 
                            waves='light' 
                            className="#03a9f4 light-blue modal-close" 
                            typae="button"
                            onClick={this.handleFormSubmit}
                            >
                          Submit
                        </Button>
                      </form>  
                    </Row>
                  </Modal>
          </Navbar>
      </div>
    );  
  }
}

export default Nav;



